  methods: {
    messageRoute() {
      this.$router.push("messages");
    },
    scrollToBlockHandler(block) {
      console.log(block);
      if (block.block == 1) {
        this.scrollTo("about-block");
      } else if (block.block == 2) {
        this.scrollTo("pastor-block");
      } else if (block.block == 3) {
        this.scrollTo("give-block");
      } else if (block.block == 4) {
        this.scrollTo("location-block");
      } else if (block.block == 5) {
        this.scrollTo("footer2-block");
      }
    },
    scrollTo(className) {
      var target = document.getElementsByClassName(className)[0];
      target.scrollIntoView({ behavior: "smooth" });
    }
  },
};
