<template>
  <div class="select-container">
    <div @click="clickHandler" class="select-wrapper">
      <p class="select-label noselect">{{label}}</p>
      <p class="current-label noselect">{{current}}</p>
      <img :class="{opened: opened}" class="down-icon noselect" src="../assets/down_ico.svg" alt />
    </div>
    <div :class="{opened: opened}" class="items-container" data-scrollbar>
      <div class="items-wrapper">
        <p
          :key="item"
          v-for="(item,index) in data"
          @click="selectHandler(index)"
          class="select-item"
        >{{item}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import Scrollbar from "smooth-scrollbar";
export default {
  mounted() {
    Scrollbar.initAll();
    this.current = this.data[this.initial];
  },
  props: {
    label: String,
    data: Array,
    initial: String
  },
  data() {
    return {
      opened: false,
      current: String
    };
  },
  methods: {
    clickHandler() {
      if (!this.opened) {
        this.opened = true;
      } else {
        this.opened = false;
      }
    },
    selectHandler(id) {
      this.current = this.data[id]
      this.opened = false;
    }
  }
};
</script>

<style scoped>
.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.select-container {
  cursor: pointer;
  z-index: 69;
  position: relative;
  width: 240px;
  height: 40px;
  border-radius: 40px;
  margin: auto;
}
.select-container:hover {
  transition: 0.1s ease-in-out;
  box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.082);
}
.down-icon {
  transition: 0.2s ease-in-out;
}
.opened.down-icon {
  animation: none;
  height: initial;
  transition: 0.1s ease-in-out;
  box-shadow: none;
  transform: rotate(180deg);
}
.select-wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  font-family: "Montserrat";
  background: #e9e9e9;
  border-radius: 40px;
  justify-content: center;
  color: #4b4b4b;
}
.items-container {
  cursor: default;
  z-index: -1;
  position: relative;
  padding: 0 16px;
  background: rgb(241, 241, 241);
  text-align: center;
  top: -20px;
  border-radius: 0 0 20px 20px;
  overflow: auto;
  height: 0px;
  transition: 0.1s ease-in-out;
}
.items-wrapper {
  height: 300px;
}
.opened {
  height: 200px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.171);
}
.select-label {
  color: #949494;
}
.current-label {
  margin: 0 4px;
}
.select-item {
  cursor: pointer;
  font-family: "Montserrat";
  text-align: center;
  padding: 8px;
  background-color: rgb(219, 219, 219);
  margin: 8px;
  border-radius: 40px;
}
.select-item:hover {
  transition: 0.1s ease-in-out;
  background-color: rgb(197, 197, 197);
}
.select-item:first-of-type {
  margin-top: 40px;
}
.select-item:last-of-type {
  margin-bottom: 40px;
}
@media (max-width: 1280px) {
  .select-container {
    height: 32px;
  }
  .select-label,
  .current-label,
  .select-item {
    font-size: 14px;
  }
  .select-container {
    width: 180px;
  }
}
</style>