<template>
  <div class="button-container" @click="clickHandler">
    <slot></slot>
  </div>
</template>

<script>
export default {
  methods: {
    clickHandler() {
      this.$emit("buttonClicked", this.btnId);
      console.log("clicked btn with id:", this.btnId);
    }
  },
  props: {
    btnId: null
  }
};
</script>

<style scoped>
.button-container {
  display: table;
  background-color: white;
  width: fit-content;
  padding: 20px 40px;
  border-radius: 100px;
  font-family: "Montserrat";
  font-weight: bold;
  color: #565656;
  font-size: 16px;
}
.button-container:hover {
  cursor: pointer;
  box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.082);
}
@media (max-width: 1280px) {
  .button-container {
    padding: 16px 36px;
    font-size: 12px;
  }
}
</style>