import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Messages from "../views/Messages.vue";

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "home",
        component: Home,
    },
    {
        path: "/messages",
        name: "messages",
        component: Messages,
    },
];

const router = new VueRouter({
    mode: 'history',
    routes,
});

export default router;