<template>
  <div class="nav-container">
    <div class="link-container">
      <a href>facebook</a>
      <a href>instagram</a>
      <a href>forum</a>
    </div>
    <div class="logo-container"></div>

    <div class="menu-container">
      <p id="menu-label">menu</p>
      <img @click="menuClicked" src="../assets/menu_ico.svg" class="menu-icon" alt />
    </div>
    <menupane :opened="opened" v-on:closeclicked="closeClicked" @scrollToBlock="scrollToBlock"/>
  </div>
</template>

<script>
import menupane from "../components/MenuPane";

export default {
  components: { menupane },
  methods: {
    menuClicked() {
      this.opened = true;
    },
    closeClicked() {
      this.opened = false;
    },
    scrollToBlock(data){
      this.$emit("scrollToBlock",{block: data.block});
      this.opened = false
    }
  },
  data() {
    return {
      opened: false
    };
  }
};
</script>

<style scoped>
.nav-container {
  width: 100%;
  height: 130px;
  display: grid;
  grid-template-columns: 33.33% 33.33% 33.33%;
  padding: 48px 0 48px 0;
}
.logo-container {
  width: 58px;
  height: 58px;
  border-radius: 58px;
  background-color: #ba7c51;
  justify-self: center;
}
.link-container {
  display: flex;
}
.link-container > a {
  margin-right: 40px;
  align-self: center;
}
.menu-container {
  justify-self: right;
  display: flex;
  align-self: center;
}
#menu-label {
  height: fit-content;
  padding-right: 24px;
  align-self: center;
}
#menu-label,
.link-container > a {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #3b3b3b;
}
.menu-icon {
  cursor: pointer;
  transition: 0.2s;
}
.menu-icon:hover {
  transform: scale(1.1);
}
@media (max-width: 1280px) {
  .nav-container {
    padding: 36px 0 36px 0;
  }
  .menu-icon {
    width: 40px;
  }
  #menu-label,
  .link-container > a {
    font-size: 13px;
  }
}

@media (max-width: 900px) {
  .link-container {
    visibility: hidden;
  }
}

@media (max-width: 650px) {
}
</style>