<template>
  <div @click="clickHandler" :class="{reveal:reveal}" class="return-container">
    <img class="return-icon" src="../assets/return_ico.svg" alt />
  </div>
</template>

<script>
export default {
  mounted() {
    window.onscroll = this.scrollListener;
  },
  methods: {
    scrollListener() {
      if (window.scrollY > 100) {
        this.reveal = true;
      } else {
        this.reveal = false;
      }
    },
    clickHandler() {
      window.scrollTo({
        top: 0,
        left: 0,
        behavior: "smooth"
      });
    }
  },
  data() {
    return {
      reveal: false
    };
  }
};
</script>

<style scoped>
.return-container {
  visibility: hidden;
  display: table;
  position: fixed;
  bottom: 0;
  right: 0px;
  cursor: pointer;
}
.return-container.reveal {
  visibility: visible;
  animation: openPane 0.2s ease-out;
}
.return-icon:hover {
  transition: 0.3s;
  transform: scale(1.05);
}
@keyframes openPane {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
</style>